.auth-page__background {
  position: relative;
  height: 100%;
  width: 100%;
  overflow: hidden;
}

.auth-page__background .glow-point-left {
  position: absolute;
  width: 635px;
  height: 635px;
  left: -20%;
  top: -6%;
  background: #7d76a9;
  opacity: 0.3;
  filter: blur(150px);
}

.auth-page__background .glow-point-right {
  position: absolute;
  width: 898px;
  height: 898px;
  left: 80%;
  top: -10%;
  background: #8ceaff;
  opacity: 0.3;
  filter: blur(150px);
  transform: matrix(-1, 0, 0, 1, 0, 0);
}

.auth-page__background .star {
  position: absolute;
  width: 24px;
  height: 24px;
}

.auth-page__background .star.one {
  top: 20%;
  left: 5%;
}

.auth-page__background .star.two {
  top: 6%;
  left: 60%;
}

.auth-page__background .star.three {
  top: 80%;
  left: 85%;
}

.auth-form__container {
  min-height: 100vh;
  overflow-y: auto;
  overflow-x: hidden;
  display: flex;
  justify-content: center;
  align-items: center;
}

.auth-form {
  position: relative;
  max-width: 575px;
  width: 100%;
  background-color: var(--sidebar-bg-color);
  border-radius: 16px;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 72px 50px;
  gap: 30px;
  margin: 24px auto;
}

.auth-form__logo a {
  font-family: 'Anek Bangla';
  font-style: normal;
  font-weight: 500;
  font-size: 28.909px;
  line-height: 58px;
  color: var(--white-color);
  display: flex;
}

.auth-form__logo a img {
  margin-right: 15px;
}

.auth-form__title {
  font-weight: 900;
  font-size: 24px;
  line-height: 140%;
  color: var(--white-color);
}

.auth-form__content {
  max-width: 320px;
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 30px;
}

.auth-form__divider {
  height: 1px;
  background-color: var(--divider);
  width: 100%;
}

.auth-form__text-fields {
  display: flex;
  flex-direction: column;
  width: 100%;
  gap: 15px;
}

.auth-form__button-container {
  display: flex;
  gap: 14px;
  width: 100%;
}

.auth-form__gmail {
  cursor: pointer;
  flex: 1;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 8px;
  height: 38px;
  background: #333438;
  border: 1px solid #494a4f;
  border-radius: 4px;
  font-weight: 500;
  font-size: 16px;
  line-height: 26px;
  color: var(--white-color);
}

.auth-form__gmail:active,
.auth-form__gmail:hover {
  border: 1px solid #494a4f;
}

.auth-form__gmail img {
  max-height: 18px;
}

.auth-form__checkbox {
  display: flex;
  gap: 8px;
  align-items: flex-start;
  color: var(--white-color);
  font-style: normal;
  font-weight: 500;
  font-size: 14px;
  line-height: 17px;
}

.auth-form__checkbox input {
  border-radius: 2px;
  outline: none;
  height: 24px;
  width: 24px;
  background-color: transparent;
}

.auth-form__checkbox p {
  margin-bottom: 0;
}

.auth-form__footer {
  width: 100%;
  font-weight: 500;
  font-size: 16px;
  line-height: 26px;
}

.auth-form__footer p {
  color: var(--white-color);
  margin-bottom: 0px;
}

.auth-form__footer div a {
  color: var(--btn-link-color);
}